@import url("https://fonts.googleapis.com/css2?family=Mulish:ital,wght@0,200..1000;1,200..1000&family=Roboto+Mono:ital,wght@0,100..700;1,100..700&display=swap");

$content-width: 65%;
$header-height: 50px;
$font-style: "Avenir", BlinkMacSystemFont, sans-serif;
$code-font-style: "Roboto Mono", monospace;
$heading-font: "Mulish", sans-serif;
$nav-height: 20px;

// Breakpoints
$breakpoint-mobile: 415px;
$breakpoint-small-tablet: 750px;

// CSS custom properties, will change with system theme.
:root {
  --bg-color: rgb(255, 255, 255);
  --highlight-color: #fffb00;
  --font-color: #000000;
  --inverted-font-color: var(--font-color);
  --link-color: var(--font-color);
  --caption-color: #727375;
  --emph: #e61726;
  --code-color: var(--emph);
  --nav-color: var(--emph);
  --heading-font-color: #425469;
  --header: #000000;
  --award-color: #0cac1c;
  --ponder-color: #d3a10b;
  --ponder-background-color: rgba(255, 235, 130, 0.2);
  --todo-color: rgb(148, 2, 206);
  --todo-background-color: rgba(237, 146, 134, 0.2);
  --reading-background-color: #bcf6bc;
  --reading-color: rgb(3, 76, 3);
  --note-color: rgb(0, 107, 161);
  --note-background-color: rgba(159, 213, 239, 0.2);
  --callout-background-color: rgba(243, 183, 183, 0.197);
  --table-alternate-bg-color: lightgrey;
}

*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
