@media only screen and (max-height: 420px) and (max-width: $breakpoint-small-tablet) and (orientation: landscape) {
  main {
    max-width: 80%;
  }

  header {
    flex-direction: row;
    height: 60vh;

    .info {
      flex-direction: column;
      font-size: 0.7em;
    }

    > * {
      margin: 10px;
    }
  }

  img,
  figcaption {
    max-width: 95vw;
  }

  img,
  figcaption {
    max-width: 95vw;
  }

  .blurb {
    padding: 0 3px;
  }
}

@media screen and (max-width: $breakpoint-mobile) and (orientation: portrait) {
  details {
    max-width: 100%;
  }

  aside {
    nav {
      font-size: 0.8em;

      ul > li > a.nav-link {
        padding: 8px 8px;
      }
    }
  }

  blockquote {
    margin: 10px;
    padding: 0 20px;
  }

  main {
    min-width: 100%;
    padding: 10px;
  }

  .artifact-list {
    margin-left: 0;
  }

  footer {
    .email {
      font-size: 0.8em;
    }
  }

  header {
    h1 {
      font-size: 1.5em;
    }

    .email {
      font-size: 0.7em;
    }

    img {
      width: 30vw;
      height: auto;
    }
  }

  h1 {
    font-size: 1.5em;
  }

  ul,
  ol {
    padding-left: 0.8em;
  }

  .blurb {
    min-width: 100%;
    padding: 0 0;
    font-size: 0.9em;
  }

  aside {
    width: 100%;

    nav {
      font-size: 0.7em;

      ul {
        li {
          a.nav-link {
            padding: auto 7px auto 7px;
          }
        }
      }
    }
  }

  img,
  figcaption {
    max-width: 95vw;
  }

  img {
    margin-left: auto;
    margin-right: auto;
    width: auto;
    max-height: 60vh;
  }
}
