body {
  background-color: var(--bg-color);
  min-width: $content-width;
  color: var(--font-color);
  text-rendering: optimizeLegibility;
  font-family: $font-style;
  -webkit-font-smoothing: antialiased;
  font-size: 1rem;
  line-height: 1.6;
}

strong {
  font-weight: 1000;
}

h2,
h3,
h4 {
  font-weight: 700;
  color: var(--heading-font-color);
  font-family: $heading-font;
  margin: auto;
  padding-top: 5px;
}

h1 {
  color: var(--header);
  font-family: $heading-font;
  a,
  a:visited {
    text-decoration: none;
  }
}

a,
a:visited {
  color: var(--link-color);
  text-decoration: underline;

  &:not(:has(img)):hover {
    text-decoration: var(--emph) underline;
    text-decoration-thickness: 2px;
  }
}

// Not named .highlight to not collide with syntax highlighting.
.hlt {
  background-color: var(--highlight-color);
  color: var(--inverted-font-color);
  a {
    color: var(--inverted-font-color);
  }
}

.blurb {
  margin-bottom: 20px;
  font-size: 1em;
  padding: 0 2em;
  max-width: 90%;
}

header {
  padding: 0px 15px;
  text-align: center;
  margin: $header-height 0 0;
  height: min(40vh, 200px);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  &.pages {
    height: auto;
  }

  img {
    border-radius: 360px;
    height: min(35vh, 150px);
    max-height: 200px;
    width: auto;
  }

  h1 {
    margin-bottom: 2px;
    margin-left: 5px;
    margin-top: 0px;
  }
}

main {
  padding: 15px;
  width: 80%;
  max-width: 60em;
  margin: auto;
}

// Write ups (posts and coursenotes)

article {
  margin: auto;
  max-width: 50em;
}

/* Aside */

aside {
  background: var(--bg-color);
  width: 100%;
  z-index: 2;
  border-bottom: 1px solid var(--emph);
  box-shadow: 0px 1px 1px RGBA(4, 25, 54, 0.1);

  nav {
    display: flex;
    justify-content: center;
    font-size: 1em;

    ul {
      margin: 0;
      padding: 0;
      list-style: none;

      li {
        float: left;
        position: relative;

        a.nav-link {
          text-decoration: none;
          color: var(--nav-color);
          display: block;
          padding: 15px;
          font-family: $heading-font;
          line-height: $nav-height;
          margin-bottom: -1px;
          box-shadow: 0;
          transition: 0.3s;
          -webkit-transition: 0.3s;

          &:hover,
          &.active {
            text-decoration: none;
            color: var(--emph);
          }
        }
      }
    }

    &:after {
      content: "";
      display: table;
      clear: both;
    }
  }
}

.sub {
  font-size: 0.95em;
  margin-top: 0;
  margin-bottom: 5px;

  a {
    margin: 2px;
  }
}

.emph {
  color: var(--emph);
}

figure {
  margin: 25px auto;
  display: grid;
  justify-content: center;
  text-align: center;

  figcaption {
    padding: 0;
    color: var(--caption-color);
    font-family: Avenir;
    text-align: center;
    margin-top: 2vw;
  }

  img {
    max-width: 90%;
    margin: auto;
  }
}

ul {
  list-style-type: circle;
}

.contact {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;

  a,
  a:visited {
    font-size: 1.5em !important;
    text-decoration: none;
  }
}

footer {
  text-align: center;
  padding: 40px;

  .address {
    font-size: 0.8em;
  }
}

.nav-links {
  text-align: center;
  a {
    padding: 0 5px;
  }
}

.fa,
.fab,
.far,
.fas,
.ai {
  margin: 5px;
  text-decoration: none;
}

.visible-hidden {
  clip: rect(1px, 1px, 1px, 1px);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 0px;
}

.visible-hidden:focus {
  clip: auto;
  height: auto;
  overflow: auto;
  position: absolute;
  width: auto;
}

.artifact-list {
  margin-top: 0;
  margin-left: 30px;
  list-style-type: none;
  li:not(:first-child) {
    margin-top: 5px;
  }

  .artifact {
    padding-bottom: 10px;
  }

  .authors {
    font-size: 0.85em;
    color: var(--emph);
  }

  details {
    & > summary {
      outline: none;
      margin-bottom: 5px;
    }

    p {
      padding: 5px;
      border: 1px grey solid;
      border-radius: 5px;
      color: var(--font-color);
      list-style-type: none;
    }
  }
}

details > summary {
  cursor: pointer;
}

.banner {
  padding: 5px;
  border-radius: 5px;
  border: var(--emph) solid 2px;
  @extend .hlt;
}

code {
  font-family: $code-font-style;
  font-weight: 450;
  font-size: 0.9em;

  &:not(.highlight code) {
    color: var(--code-color);
  }

  h1 > &,
  h2 > &,
  h3 > & {
    font-size: inherit;
  }

  a:hover > & {
    color: var(--inverted-font-color);
  }
}

pre:not(.mermaid) {
  padding: 10px;
  border-radius: 5px;
  border: 1pt solid var(--heading-font-color);
  line-height: 1.5;
}

table {
  margin: 20px auto;
  border-collapse: collapse;
  font-size: 0.9em;
  border-bottom: 2pt solid var(--font-color);

  th {
    border-top: 2pt solid var(--font-color);
    border-bottom: 2pt solid var(--font-color);
  }

  tr {
    border-bottom: 0.5pt solid;
  }

  tr > td,
  tr > th {
    padding: 0.5em;
    &:not(:last-child) {
      border-right: 1pt solid;
    }
  }
}

.course {
  padding-top: 5px;
  max-width: 120ch;
}

.released {
  &::before {
    content: "RELEASED";
  }
  font-weight: bold;
  background-color: green;
  color: white;
}

.due {
  &::before {
    content: "DUE";
  }
  font-weight: bold;
  background-color: firebrick;
  color: white;
}

.in-class {
  &::before {
    content: "IN CLASS";
  }
  font-weight: bold;
  background-color: rgb(41, 157, 203);
  color: white;
}

.callout {
  overflow: hidden;
  border-left: 4px solid var(--callout-border-color, var(--emph));
  background-color: var(--callout-bg-color, var(--callout-background-color));
  border-radius: 4px;
  padding: 0.5em 1em;
  list-style-type: none;

  &::before {
    display: block;
    font-weight: bold;
    font-size: 0.85em;
    font-family: $heading-font;
    content: var(--callout-label);
    color: var(--callout-color);
  }

  &.note {
    --callout-label: "NOTE";
    --callout-color: var(--note-color);
    --callout-bg-color: var(--note-background-color);
    --callout-border-color: var(--note-color);
  }

  &.todo {
    --callout-label: "TASK";
    --callout-color: var(--todo-color);
    --callout-bg-color: var(--todo-background-color);
    --callout-border-color: var(--todo-color);
  }

  &.ponder {
    --callout-label: "PONDER";
    --callout-color: var(--ponder-color);
    --callout-bg-color: var(--ponder-background-color);
    --callout-border-color: var(--ponder-color);
  }

  &.reading {
    --callout-label: "READING";
    --callout-color: var(--reading-color);
    --callout-bg-color: var(--reading-background-color);
    --callout-border-color: var(--reading-color);
  }
}

blockquote {
  border: 1pt dashed var(--emph);
  padding: 0 5px 0 5px;
}

.footnotes {
  &::before {
    font-size: 1.2em;
    content: "Footnotes";
  }

  :target {
    @extend .hlt;
  }

  font-size: 0.9em;
}

sup[id^="fnref:"] {
  padding-right: 0.5em;
}

.fn-tooltip {
  position: absolute;
  font-size: 0.9em;
  max-width: 25em;
  border: 1px solid var(--font-color);
  padding: 5px;
  border-radius: 4px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  z-index: 1000;
  background-color: ivory;
  color: var(--inverted-font-color);

  a {
    color: var(--inverted-font-color);
  }

  code {
    color: var(--inverted-font-color) !important;
  }
  // @extend .hlt;
}

// Two-column layout utility classes
.two-column-container {
  @media only screen and (min-width: 1000px) {
    display: flex;
    gap: 40px;
    align-items: flex-start;
  }
}

.left-column {
  @media only screen and (min-width: 1000px) {
    position: sticky;
    top: 20px;
    max-height: calc(100vh - 40px);
    overflow-y: auto;
    width: 200px;
    flex-shrink: 0;
  }
}

.right-column {
  @media only screen and (min-width: 1000px) {
    flex: 1;
    min-width: 0;
  }
}

// Increase page width for two-column layouts
main:has(.two-column-container) {
  @media only screen and (min-width: 900px) {
    max-width: 90em;
  }
}
